.footer {
	position: relative;
	background: #000; }

.footer-overlay {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	background: rgba(64, 67, 77, 0.9); }

.footer-second-overlay {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	background: rgba(0,0,0,0.2); }

.footer-content {
	position: relative;
	display: flex;
	flex-direction: column;
	align-items: center;
	padding: 30px 0;
	z-index: 35; }

.footer-links {
	display: flex;
	align-items: center;
	padding: 15px 0;

	> a {
		text-decoration: none;
		margin-right: 10px;
		color: #909ea8;
		@include lato();
		font-weight: 300;
		letter-spacing: 1px;
		font-size: 16px;
		cursor: pointer;
		transition: color .3s ease-out;

		&:hover {
			color: #d3d3d4; } } }


.footer-social {
	display: flex;
	align-items: center;
	justify-content: space-around;
	width: 50%;
	padding: 15px 0;

	a {
		display: flex;
		align-items: center;
		justify-content: center;
		text-decoration: none;
		height: 35px;
		width: 35px;
		border-radius: 40px;
		box-shadow: 0 0 0 3px #909ea8;
		cursor: pointer;
		transition: all .4s ease-out;

		span {
			color: #909ea8;
			transition: color .4s ease-out; }

		&:hover {
			box-shadow: 0 0 0 6px #fff;

			span {
				color: #fff; } } } }

.footer-copyright {
	padding: 15px 0;

	p {
		@include lato();
		font-weight: 300;
		letter-spacing: 1px;
		font-size: 16px;
		color: #d3d3d4;

		span {
			color: #909ea8;
			font-size: 15px; } } }



@media screen and (min-width: 640px) {
	.footer-links {
		a {
			font-size: 18px; } } }
