html { box-sizing: border-box; -webkit-text-size-adjust: 100%; -ms-text-size-adjust: 100%; width: 100vw; }

body { margin: 0; overflow-x: hidden; width: 100vw; -moz-osx-font-smoothing: grayscale; -webkit-font-smoothing: antialiased; }

* { background-repeat: no-repeat; padding: 0; margin: 0; }

*, ::before, ::after { box-sizing: inherit; }

::before, ::after { text-decoration: inherit; vertical-align: inherit; }

audio:not([controls]) { display: none; height: 0; }

hr { overflow: visible; border: none; }

article, aside, details, figcaption, figure, footer, header, main, menu, menu, nav, section, summary { display: block; }

summary { display: list-item; }

small { font-size: 80%; }

[hidden], template { display: none; }

abbr[title] { border-bottom: 1px dotted; text-decoration: none; }

a { background-color: transparent; -webkit-text-decoration-skip: objects; }

a:active, a:hover { outline-width: 0; }

b, strong { font-weight: bolder; }

dfn { font-style: italic; }

mark { background-color: #ff0; color: #000; }

sub, sup { font-size: 75%; line-height: 0; position: relative; veritcal-align: baseline; }

sub { bottom: -0.25em; }

sup { top: -0.5em; }

button, [type="button"], [type="reset"], [type="submit"], [role="button"] { cursor: pointer; }

[disabled] { cursor: default; }

[type="search"] { -webkit-appearance: textfield; }

[type="search"]::-webkit-search-cancel-button, [type="search"]::-webkit-search-decoration { -webkit-apearance: none; }

textarea { overflow: auto; resize: vertical; }

input, optgroup, select, textarea { font: inherit; }

outgroup { font-weight: bold; }

select { -moz-appearance: none; -webkit-appearance: none; }

select::-ms-expand { display: none; }

legend { border: 0; color: inherit; display: table; max-width: 100%; white-space: normal; }

::-webkit-file-upload-button { -webkit-appearance: button; font: inherit; }

img { border-style: none; }

progress { vertical-align: baseline; }

svg:not(:root) { overflow: hidden; }

audio, canvas, progress, video { display: inline-block; }

@media screen { [hidden~="screen"] { display: inherit; }
  [hidden~="screen"]:not(:active):not(:focus):not(:target) { position: absolute !important; clip: rect(0, 0, 0, 0) !important; } }

[aria-busy="true"] { cursor: progress; }

[aria-controls] { cursor: pointer; }

[aria-disabled] { cursor: default; }

::-moz-focus-inner { padding: 0; border: 0; }

.nav { position: relative; display: flex; align-items: stretch; height: 70px; z-index: 50; }

.nav-left { display: flex; flex: 1; flex-basis: 0; align-items: center; justify-content: flex-start; padding-left: 20px; }

.nav-right { display: flex; flex: 1; flex-basis: 0; align-items: center; justify-content: flex-end; padding-right: 20px; }

.nav-item { display: flex; align-items: center; justify-content: center; }

.nav-item .nav-logo { color: #909ea8; }

.nav-item .menu-icon { display: flex; align-items: center; justify-content: center; height: 40px; width: 50px; border-left: 1px solid #909ea8; cursor: pointer; }

.nav-item .menu-icon i { font-size: 32px; color: #909ea8; }

.footer { position: relative; background: #000; }

.footer-overlay { position: absolute; top: 0; left: 0; width: 100%; height: 100%; background: rgba(64, 67, 77, 0.9); }

.footer-second-overlay { position: absolute; top: 0; left: 0; width: 100%; height: 100%; background: rgba(0, 0, 0, 0.2); }

.footer-content { position: relative; display: flex; flex-direction: column; align-items: center; padding: 30px 0; z-index: 35; }

.footer-links { display: flex; align-items: center; padding: 15px 0; }

.footer-links > a { text-decoration: none; margin-right: 10px; color: #909ea8; font-family: 'Lato', sans-serif; font-weight: 300; letter-spacing: 1px; font-size: 16px; cursor: pointer; transition: color .3s ease-out; }

.footer-links > a:hover { color: #d3d3d4; }

.footer-social { display: flex; align-items: center; justify-content: space-around; width: 50%; padding: 15px 0; }

.footer-social a { display: flex; align-items: center; justify-content: center; text-decoration: none; height: 35px; width: 35px; border-radius: 40px; box-shadow: 0 0 0 3px #909ea8; cursor: pointer; transition: all .4s ease-out; }

.footer-social a span { color: #909ea8; transition: color .4s ease-out; }

.footer-social a:hover { box-shadow: 0 0 0 6px #fff; }

.footer-social a:hover span { color: #fff; }

.footer-copyright { padding: 15px 0; }

.footer-copyright p { font-family: 'Lato', sans-serif; font-weight: 300; letter-spacing: 1px; font-size: 16px; color: #d3d3d4; }

.footer-copyright p span { color: #909ea8; font-size: 15px; }

@media screen and (min-width: 640px) { .footer-links a { font-size: 18px; } }

.landing-page-content { display: flex; flex-direction: column; justify-content: center; }

.landing-header { position: relative; height: 900px; margin-top: -70px; }

.landing-header img { width: 100%; height: 350px; transform: scaleX(-1); }

.landing-header-extension { position: absolute; top: 300px; left: 0; height: 600px; width: 100%; background: #000; }

.landing-header-overlay { position: absolute; top: 0; left: 0; height: 100%; width: 100%; background: rgba(64, 67, 77, 0.9); }

.landing-header-content { position: absolute; top: 140px; left: 0; z-index: 100; width: 100%; text-align: center; }

.landing-header-content h1 { font-family: 'Open Sans', sans-serif; font-weight: 600; font-size: 30px; line-height: 45px; letter-spacing: 3px; text-transform: uppercase; color: #d3d3d4; margin-bottom: 35px; }

.landing-header-content p { font-family: 'Lato', sans-serif; font-wiehgt: 300; font-size: 16px; line-height: 30px; letter-spacing: 2px; text-transform: uppercase; color: #909ea8; }

.landing-header-links { display: flex; flex-direction: column; align-items: center; padding-top: 60px; padding-bottom: 80px; z-index: 100; width: 100%; }

.landing-header-links a { text-decoration: none; height: 45px; }

.landing-header-links a:nth-of-type(1) { margin-bottom: 45px; }

.landing-header-links a:nth-of-type(1) button { height: 45px; width: 150px; border: 1px solid; border-color: #ed8f03; border-radius: 6px; background-color: #ed8f03; transition: background-color .3s ease-out, border-color .3s ease-out; }

.landing-header-links a:nth-of-type(1) button span { font-family: 'Open Sans', sans-serif; font-weight: 600; letter-spacing: 2px; font-size: 14px; text-transform: uppercase; color: #fff; }

.landing-header-links a:nth-of-type(1) button:hover { background-color: #bb7102; border-color: #bb7102; }

.landing-header-links a:nth-of-type(2) button { display: flex; align-items: center; justify-content: center; height: 45px; width: 150px; border: 1px solid #fff; border-radius: 6px; background: transparent; }

.landing-header-links a:nth-of-type(2) button span { font-family: 'Open Sans', sans-serif; font-weight: 600; letter-spacing: 2px; font-size: 14px; text-transform: uppercase; color: #fff; }

.landing-header-links a:nth-of-type(2) button span:nth-of-type(2) { font-size: 22px; }

@media screen and (min-width: 490px) { .landing-header-links { flex-direction: row; align-items: center; justify-content: center; }
  .landing-header-links a:nth-of-type(1) { margin-bottom: 0; margin-right: 20px; }
  .landing-header-links a:nth-of-type(2) { margin-left: 20px; } }

@media screen and (min-width: 640px) { .landing-header-content h1 { width: 90%; margin: 0 auto 25px auto; }
  .landing-header-content p { font-size: 18px; } }

.service-container { display: flex; flex-direction: column; align-items: center; width: 100%; background: transparent; margin-bottom: -180px; }

.service-top-diamond { margin-top: -120px; height: 320px; width: 335px; max-width: 100vw; overflow-x: hidden; background: #fff; transform: rotate(45deg); }

.service-bottom-diamond { height: 320px; width: 335px; max-width: 100vw; background: #fff; overflow-x: hidden; transform: rotate(45deg); margin-bottom: 40px; }

.service-header { display: flex; flex-direction: column; align-items: center; width: 100%; margin-top: -230px; z-index: 105; }

.service-header h2 { font-family: 'Lato', sans-serif; font-weight: 300; font-size: 28px; letter-spacing: 2px; color: #909ea8; text-transform: uppercase; }

.service-header-underline { display: flex; justify-content: center; align-items: center; padding-top: 20px; }

.service-header-underline .left-triangle { height: 0; width: 0; border-bottom: 10px solid #d3d3d4; border-left: 85px solid transparent; margin-right: 5px; }

.service-header-underline .hex { width: 30px; height: 13px; background: #909ea8; position: relative; }

.service-header-underline .hex:before { content: ''; position: absolute; top: -8px; left: 0; width: 0; border-left: 15px solid transparent; border-right: 15px solid transparent; border-bottom: 8px solid #909ea8; }

.service-header-underline .hex:after { content: ''; position: absolute; bottom: -8px; left: 0; width: 0; height: 0; border-left: 15px solid transparent; border-right: 15px solid transparent; border-top: 8px solid #909ea8; }

.service-header-underline .right-triangle { height: 0; width: 0; border-bottom: 10px solid #d3d3d4; border-right: 85px solid transparent; margin-left: 5px; }

.service-header-text { width: 90%; padding-top: 60px; margin: 0 auto; text-align: center; }

.service-header-text p { font-family: 'Lato', sans-serif; font-weight: 300; letter-spacing: 2px; font-size: 17px; line-height: 26px; color: #82929d; }

.service-selections { display: flex; flex-wrap: wrap; justify-content: space-around; align-items: center; width: 90%; padding-top: 75px; margin: 0 auto; z-index: 35; }

.selection-hexagon { display: flex; align-items: center; justify-content: center; position: relative; width: 90px; height: 40px; margin-bottom: 60px; background: #d3d3d4; z-index: 35; }

.selection-hexagon:before { content: ''; position: absolute; top: -25px; left: 0; width: 0; height: 0; border-left: 45px solid transparent; border-right: 45px solid transparent; border-bottom: 25px solid #d3d3d4; }

.selection-hexagon:after { content: ''; position: absolute; bottom: -25px; left: 0; width: 0; height: 0; border-left: 45px solid transparent; border-right: 45px solid transparent; border-top: 25px solid #d3d3d4; }

.selection-hexagon.selection-hexagon-active { background: rgba(0, 0, 0, 0.8); }

.selection-hexagon.selection-hexagon-active:before { border-bottom-color: rgba(0, 0, 0, 0.8); }

.selection-hexagon.selection-hexagon-active:after { border-top-color: rgba(0, 0, 0, 0.8); }

.selection-hexagon.selection-hexagon-active .selection-icon span { color: rgba(0, 0, 0, 0.8); }

.selection-icon { display: flex; align-items: center; justify-content: center; height: 65px; width: 65px; border-radius: 50%; background: #fff; z-index: 30; }

.selection-icon span { font-size: 23px; color: #d3d3d4; }

.selection-border { position: relative; display: flex; width: 0; height: 120px; margin-top: 25px; margin-bottom: 25px; border: 1px dotted #909ea8; }

.service-selected { display: flex; flex-direction: column; align-items: center; padding-top: 80px; margin-bottom: -180px; z-index: 35; }

.service-selected h2 { font-family: 'Lato', sans-serif; font-weight: 300; letter-spacing: 2px; font-size: 22px; text-transform: uppercase; margin-bottom: 35px; color: #909ea8; }

.service-selected p { font-family: 'Lato', sans-serif; font-weight: 300; letter-spacing: 2px; font-size: 17px; line-height: 27px; text-align: center; color: #82929d; }

.service-selected a { position: relative; margin-top: 40px; text-decoration: none; z-index: 50; }

.service-selected a button { display: flex; align-items: center; justify-content: center; height: 45px; width: 150px; border: 1px solid #666; border-radius: 6px; background: transparent; }

.service-selected a button span { font-family: 'Open Sans', sans-serif; font-weight: 600; letter-spacing: 2px; font-size: 14px; text-transform: uppercase; color: #666; }

.service-images { position: relative; display: flex; flex-direction: column; align-items: center; padding-top: 110px; z-index: 40; }

.service-images > div { height: 140px; width: 200px; background: rgba(0, 0, 0, 0.6); margin-bottom: 120px; }

@media screen and (min-width: 490px) { .service-top-diamond { margin-top: -160px; width: 480px; height: 480px; }
  .service-bottom-diamond { width: 480px; height: 480px; margin-bottom: 0; }
  .service-header { margin-top: -410px; }
  .service-selected { margin-bottom: -350px; }
  .service-images > div { height: 185px; width: 275px; } }

@media screen and (min-width: 640px) { .service-top-diamond { width: 480px; height: 480px; margin-top: -240px; }
  .service-bottom-diamond { margin-bottom: -80px; }
  .service-selected p { width: 80%; margin: 0 auto; font-size: 18px; line-height: 32px; }
  .service-images > div { height: 210px; width: 320px; } }

.landing-testimonials { display: flex; flex-direction: column; align-items: center; width: 100%; padding-top: 290px; padding-bottom: 70px; background: #d3d3d4; }

.landing-testimonial-header { text-align: center; width: 80%; margin: 0 auto; }

.landing-testimonial-header h2 { font-family: 'Lato', sans-serif; font-weight: 300; letter-spacing: 2px; font-size: 28px; text-transform: uppercase; margin-bottom: 25px; }

.landing-testimonial-header p { font-family: 'Lato', sans-serif; font-weight: 300; letter-spacing: 1px; font-size: 18px; line-height: 27px; }

.testimonial-display { display: flex; flex-direction: column; align-items: center; width: 80%; padding-top: 70px; margin: 0 auto; }

.single-testimonial { display: flex; flex-direction: column; align-items: center; position: relative; width: 100%; height: 250px; margin-bottom: 65px; background: #fff; box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.23), 0 4px 10px 0 rgba(0, 0, 0, 0.05); }

.testimonial-cutout { width: 0; height: 0; border-left: 65px solid transparent; border-right: 65px solid transparent; border-top: 65px solid #d3d3d4; }

.testimonial-text blockquote { position: relative; padding: 20px 10px; font-family: 'Lato', sans-serif; font-weight: 300; letter-spacing: 1px; font-size: 18px; }

.testimonial-text blockquote:before { content: "\201C"; font-family: 'Open Sans', sans-serif; font-weight: 600; font-size: 23px; }

.testimonial-text p { text-align: right; padding-right: 15px; }

@media screen and (min-width: 490px) { .landing-testimonials { padding-top: 340px; } }

@media screen and (min-width: 640px) { .landing-testimonials { padding-top: 420px; } }

.landing-info { position: relative; background: #fff; padding-bottom: 30px; }

.landing-info-overlay { position: absolute; top: 0; left: 0; height: 100%; width: 100%; background: rgba(64, 67, 77, 0.9); }

.landing-info-mobile { position: relative; z-index: 100; }

.landing-info-non-mobile { display: none; }

.landing-about { width: 80%; padding-top: 60px; margin: 0 auto; text-align: center; }

.landing-about h2 { font-family: 'Lato', sans-serif; font-weight: 300; font-size: 30px; line-height: 45px; letter-spacing: 3px; text-transform: uppercase; color: #d3d3d4; margin-bottom: 20px; }

.landing-about > p { font-family: 'Lato', sans-serif; font-wiehgt: 300; font-size: 16px; line-height: 30px; letter-spacing: 2px; color: #909ea8; margin-bottom: 10px; }

.landing-about a { color: #fff; }

.landing-locations { width: 100%; padding-top: 70px; }

.landing-locations h3 { font-family: 'Lato', sans-serif; font-weight: 300; letter-spacing: 2px; font-size: 24px; line-height: 33px; text-transform: uppercase; color: #d3d3d4; margin-bottom: 13px; text-align: center; }

.landing-locations .location { display: flex; flex-direction: column; align-items: center; justify-content: center; margin-bottom: 22px; }

.landing-locations .location p { font-family: 'Lato', sans-serif; font-weight: 300; letter-spacing: 1px; line-height: 26px; font-size: 16px; color: #909ea8; }

.landing-locations .location p span { color: #fff; }

.landing-hours { width: 100%; padding-top: 70px; }

.landing-hours h3 { font-family: 'Lato', sans-serif; font-weight: 300; letter-spacing: 2px; font-size: 24px; line-height: 33px; text-transform: uppercase; color: #d3d3d4; margin-bottom: 8px; text-align: center; }

.landing-hours p { font-family: 'Lato', sans-serif; font-weight: 300; letter-spacing: 2px; font-size: 16px; line-height: 22px; color: #d3d3d4; margin-bottom: 10px; text-align: center; }

.landing-hours > div { padding-top: 35px; }

.landing-newsletter { width: 100%; padding-top: 60px; }

.landing-newsletter h3 { font-family: 'Lato', sans-serif; font-weight: 300; letter-spacing: 2px; font-size: 24px; line-height: 33px; text-transform: uppercase; color: #d3d3d4; margin-bottom: 30px; text-align: center; }

.landing-newsletter p { font-family: 'Lato', sans-serif; font-weight: 300; letter-spacing: 1px; font-size: 17px; line-height: 22px; color: #909ea8; width: 80%; text-align: center; margin: 0 auto; padding-top: 25px; }

.landing-newsletter p a { color: #fff; }

.newsletter-input { position: relative; display: flex; width: 80%; margin: 0 auto; }

.newsletter-input input { flex: 1; height: 40px; font-family: 'Lato', sans-serif; font-weight: 300; letter-spacing: 1px; font-size: 16px; color: #d3d3d4; padding-left: 12px; border: none; outline: none; background: rgba(0, 0, 0, 0.2); }

.newsletter-input input:focus + label { opacity: 0; transform: translateX(-100%); }

.newsletter-input label { position: absolute; top: 10px; left: 12px; font-family: 'Lato', sans-serif; font-weight: 300; letter-spacing: 1px; font-size: 16px; opacity: 1; color: #d3d3d4; transition: opacity .4s ease-out, transform .4s ease-out; }

.newsletter-input button { width: 40px; border: none; background: rgba(0, 0, 0, 0.3); }

.newsletter-input button span { font-size: 26px; color: #d3d3d4; }

.landing-social { padding-top: 50px; width: 100%; }

.landing-social h3 { font-family: 'Lato', sans-serif; font-weight: 300; letter-spacing: 2px; font-size: 24px; line-height: 33px; text-transform: uppercase; color: #d3d3d4; margin-bottom: 30px; text-align: center; }

.landing-social-buttons { display: flex; align-items: center; justify-content: space-around; width: 70%; margin: 0 auto; }

.landing-social-buttons a { display: flex; align-items: center; justify-content: center; height: 40px; width: 40px; border-radius: 50%; box-shadow: 0 0 0 3px #909ea8; text-decoration: none; transition: all .4s ease; }

.landing-social-buttons a button { background: none; border: none; }

.landing-social-buttons a button span { color: #909ea8; font-size: 18px; transition: color .4s ease-out; }

.landing-social-buttons a:hover { box-shadow: 0 0 0 6px #fefefe; }

.landing-social-buttons a:hover button span { color: #fefefe; }

@media screen and (min-width: 490px) { .locations { display: flex; align-items: center; justify-content: space-around; flex-wrap: wrap; padding-top: 30px; }
  .locations .location { width: 40%; margin-bottom: 45px; }
  .locations .location p { font-size: 18px; }
  .locations .location p span { display: inline-block; margin-right: 5px; }
  .locations .location:nth-of-type(5) { margin-bottom: 0; }
  .landing-hours > p { padding-top: 13px; }
  .landing-hours p { font-size: 17px; } }

@media screen and (min-width: 640px) { .landing-about p { font-size: 17px; } }
